@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.home{display:flex;flex-direction:column;text-align:center}.home__title{font-size:3rem;margin-bottom:0}.home__title span{color:#1055AF}.home__subtitle{font-size:1.5rem}.home__links{display:flex;justify-content:center}.home__btn{margin:0 10px;text-decoration:none;color:#1363cc;transition:all .3s}.home__btn:hover{color:#000}

::-webkit-resizer{display:none}textarea{resize:vertical}.chat{padding-top:0;height:88vh}.chat__container{border:1px solid #2F3031;border-top:none;max-width:1200px;margin:0 auto;margin-top:0;height:85%}.chat__time{margin:1px}.chat__form{max-width:1200px;margin:0 auto;margin-top:20px;display:flex;flex-wrap:wrap;justify-content:center;align-content:center;position:relative}.chat__text,.chat__key{border-radius:100px;background-color:#303031;border:none;color:white;font-size:14px;padding:9px;padding-left:15px;outline:none;font-family:inherit}.chat__text{display:inline-block;width:70%;margin-right:10px;padding-right:40px;max-height:200px;overflow:hidden;resize:vertical}.chat__text[contenteditable]:empty::before{content:'Type a message...';color:gray}.chat__key{width:calc(30% - 10px);height:36px}.chat__submit,.chat__key-submit{position:absolute;border-radius:100px;width:30px;height:30px;border:none;outline:none;background-color:#1363CC;cursor:pointer;transition:all .3s}.chat__submit:hover,.chat__key-submit:hover{background-color:#0c4186}.chat__submit{top:3px;right:30.5%}.chat__key-submit{position:absolute;right:5px;top:3px}.chat__account{max-width:1200px;margin:20px auto}.chat__container{padding:20px;overflow-y:scroll}.current-user .chat__time{float:right}.chat__container .chat__bubble{background-color:#2C2E30;padding:20px;border-radius:19.5px;max-width:300px;word-wrap:break-word}.chat__bubble .chat__time{font-size:12px;color:#757575;text-align:right}.chat__container .chat__bubble.current-user{margin-left:auto;background-color:#214822}.form-control{width:60%}.form-control:focus{box-shadow:none}.btn{margin-top:10px}.btn-submit{background-color:#2e7d32;color:white}@media screen and (max-width: 767px){.form-control{width:100%}.chat__container .chat__bubble{max-width:250px}}@media screen and (max-width: 600px){.chat{height:85vh}.chat__bubble{max-width:80% !important}.chat__text,.chat__key{width:100%;margin:10px;height:50px}.chat__submit{height:50px;width:50px;right:11px;top:10px}.chat__key-submit{height:50px;width:50px;right:11px;top:80px}.chat__account{display:none}}

.signup__form{max-width:1200px;margin:0 auto;padding-left:15%}.signup__title{color:#1363CC;text-align:center;margin-left:-15vw;margin-bottom:50px;margin-top:50px}.signup__title-link{text-decoration:none;color:#fff;background-color:#1363CC;padding:5px;border-radius:8px;border:3px solid #1363CC;transition:all .3s}.signup__title-link:hover{color:#1363CC;background-color:#000}.signup__input{border-radius:100px;background-color:#303031;border:none;color:white;font-size:14px;padding:9px;padding-left:15px;outline:none;font-family:inherit;width:40%;margin:10px}.signup__button{background-color:#1363CC;color:#fff;padding:0px 15px;font-size:1.5rem;border:none;outline:none;height:40px;border-radius:3px;cursor:pointer}.signup__button:hover{background-color:#0c4186}.signup__button--submit{width:140px;margin-left:10px;margin-bottom:20px;border-radius:100px}.signup__button--google{margin-right:20px;background-color:#0054E0}.signup__button--google:hover{background-color:#003794}.signup__button--github{background-color:#323131}.signup__button--github:hover{background-color:#0b0b0b}.signup__link{font-weight:bold;font-size:1.25rem;text-decoration:none;color:#1363CC}.signup__link:hover{color:#0c4186}@media screen and (max-width: 600px){.signup__form{padding:10px}.signup__input{height:50px;margin:10px 0;width:100%}.signup__button{height:50px;margin:10px;width:calc(50% - 20px)}.signup__button--submit{height:50px;margin:0;width:100%}}

.login__form{max-width:1200px;margin:0 auto;padding-left:15%}.login__title{color:#1363CC;text-align:center;margin-left:-15vw;margin-bottom:50px;margin-top:50px}.login__title-link{text-decoration:none;color:#fff;background-color:#1363CC;padding:5px;border-radius:8px;border:3px solid #1363CC;transition:all .3s}.login__title-link:hover{color:#1363CC;background-color:#000}.login__input{border-radius:100px;background-color:#303031;border:none;color:white;font-size:14px;padding:9px;padding-left:15px;outline:none;font-family:inherit;width:40%;margin:10px}.login__button{background-color:#1363CC;color:#fff;padding:0px 15px;font-size:1.5rem;border:none;outline:none;height:40px;border-radius:3px;cursor:pointer}.login__button:hover{background-color:#0c4186}.login__button--submit{width:100px;margin-left:10px;margin-bottom:20px;border-radius:100px}.login__button--google{margin-right:20px;background-color:#0054E0}.login__button--google:hover{background-color:#003794}.login__button--github{background-color:#323131}.login__button--github:hover{background-color:#0b0b0b}.login__link{font-weight:bold;font-size:1.25rem;text-decoration:none;color:#1363CC}.login__link:hover{color:#0c4186}@media screen and (max-width: 600px){.login__form{padding:10px}.login__input{height:50px;margin:10px 0;width:100%}.login__button{height:50px;margin:10px;width:calc(50% - 20px)}.login__button--submit{height:50px;margin:0;width:100%}}

.header{display:flex;justify-content:space-between;align-items:center;padding:0 10px;background-color:#343739;height:6vh;border-bottom:1px solid #6b9fff}.header__brand{font-size:2rem;text-decoration:none;font-weight:700;color:#1363cc;transition:all .3s}.header__brand:hover{color:#000}.header__item{display:flex;align-items:center}.header__link{text-decoration:none;background-color:#1363CC;color:white;border:none;padding:10px 0;font:500 20px Arial;margin:5px;border-radius:3px;cursor:pointer;transition:all .3s;width:100px;text-align:center}.header__link:hover{background-color:#000;color:#3e8aed}@media screen and (max-width: 600px){.header{height:10vh}}

.footer{display:flex;justify-content:flex-end;padding:0 10px;height:5vh}.footer__container{background-color:rgba(0,0,0,0.8);color:white;position:absolute;bottom:0;width:100%}@media screen and (max-width: 600px){.footer{display:none}}

